import { Container, Nav, Navbar, Row, Col } from "react-bootstrap";
import React, { useState } from "react";
import "./App.css";
import bg from "./img/bg.png";
import data from "./data.js";
import { Routes, Route, useNavigate } from "react-router-dom";
import Detail from "./routers/detail.js";
import Cart from './routers/Cart.js'
// import axios from 'axios'

function App() {
  let [shoes, setShoes] = useState(data);
  let navigate = useNavigate();

  return (
    <div className="App">
      <Navbar bg="light" variant="light">
        <Container>
          <Navbar.Brand onClick={()=>{navigate('/')}}>ChoShop</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={()=>{navigate('/')}}>Home</Nav.Link>
            <Nav.Link onClick={()=>{navigate('/detail/0')}}>Shoes</Nav.Link>
            <Nav.Link onClick={()=>{navigate('/cart')}}>Cart</Nav.Link>
          </Nav>
        </Container>
      </Navbar>

      <Routes>
        <Route path="/" element={
          <>
            <div className="main-bg" style={{ backgroundImage: "url(" + bg + ")" }} ></div>
            <div>
              <Container style={{ textAlign: "center" }}>
                <Row>
                  {shoes.map((_, index) => {
                    return <Card key={index} shoes={shoes[index]} i={index} />;
                  })}
                </Row>
              </Container>
            </div>
            {/* <button onClick={()=> {
              axios.get('https://codingapple1.github.io/shop/data2.json').then((getData)=>{
                let copy = [...shoes, ...getData.data]
                setShoes(copy)
              })
              axios.post('/s', { name : 'kim'})
            }}>버튼</button> */}
          </>
        } />
        <Route path="/detail/:id" element={<Detail shoes={shoes} />} />

        <Route path="/cart" element={<Cart/>}/>

        <Route path="*" element={<div>없는페이지에요</div>}></Route>
      </Routes>
      
    </div>
  );

  function Card(props) {
    return (
      <Col onClick={()=>{
        navigate('/detail/'+ props.i)
      }}>
        <img
          src={
            "https://codingapple1.github.io/shop/shoes" + (props.i + 1) + ".jpg"
          } alt="shoesimage"
          width="80%"
        ></img>
        <h4>{props.shoes.title}</h4>
        <p>{props.shoes.price.toLocaleString()}</p>
      </Col>
    );
  }
}

export default App;
