import {Table} from 'react-bootstrap'
import React from "react"
import { useDispatch, useSelector } from 'react-redux'
import { ageUp } from './../store/userSlice.js'
import { countUp } from './../store/cartSlice.js'

function Cart() {

  let data = useSelector((state)=> state )
  let dispatch = useDispatch();


  return (
    <div>
      <button onClick={()=>{dispatch(ageUp(1))}}>버튼</button>
      {data.user.age}
      <Table>
        <thead>
          <tr>
            <th>#</th>
            <th>상품명</th>
            <th>수량</th>
            <th>변경하기</th>
          </tr>
        </thead>
        <tbody>
          {
            data.cart.map((_,index)=>{
              return(
                <tr key={index}>
                  <td>{data.cart[index].id}</td>
                  <td>{data.cart[index].name}</td>
                  <td>{data.cart[index].count}</td>
                  <td><button onClick={()=>{
                    dispatch(countUp(data.cart[index].id))
                  }}>+</button></td>
                </tr>
              )
            })  
          }
        </tbody>
      </Table>
    </div>
  )
}

export default Cart