import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Nav } from "react-bootstrap";
import "../App.css";
import { addCart, countUp } from "./../store/cartSlice.js";
import { useDispatch, useSelector } from "react-redux";

function Detail(props) {
  let data = useSelector((state) => state);
  let dispatch = useDispatch();

  useEffect(() => {
    let a = setTimeout(() => {
      setAlert1(false);
    }, 2000);
    return () => {
      clearTimeout(a);
    };
  }, []);

  let [wrap, setWrap] = useState("");
  useEffect(() => {
    let a = setTimeout(() => {
      setWrap("end");
    }, 50);
    return () => {
      clearTimeout(a);
      setWrap("");
    };
  }, []);

  let { id } = useParams();
  let [alert1, setAlert1] = useState(true);
  let shoes = props.shoes.find(function (x) {
    return x.id == id;
  });
  let [tab, setTab] = useState(0);

  return (
    <div className={"container start " + wrap}>
      {alert1 === true ? (
        <div className="alert alert-warning">2초이내 구매시 할인</div>
      ) : null}
      <div className="row">
        <div className="col-md-6">
          <img
            src={
              "https://codingapple1.github.io/shop/shoes" +
              (Number(id) + 1) +
              ".jpg"
            }
            width="100%"
            alt="shoesimage"
          />
        </div>
        <div className="col-md-6">
          {/* <input onChange={(e) => { setNum(e.target.value) }} /> */}
          <h4 className="pt-5">{shoes.title}</h4>
          <p>{shoes.content}</p>
          <p>{shoes.price.toLocaleString()}원</p>
          <button
            className="btn btn-danger"
            onClick={() => {
              dispatch(addCart({ id: 1, name: "Grey Yordan", count: 1 }));
            }}
          >
            주문하기
          </button>
        </div>
      </div>
      <Nav variant="tabs" defaultActiveKey="link0">
        <Nav.Item>
          <Nav.Link
            eventKey="link0"
            onClick={() => {
              setTab(0);
            }}
          >
            버튼0
          </Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link
            eventKey="link1"
            onClick={() => {
              setTab(1);
            }}
          >
            버튼1
          </Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link
            eventKey="link2"
            onClick={() => {
              setTab(2);
            }}
          >
            버튼2
          </Nav.Link>
        </Nav.Item>
      </Nav>
      <TabContent tab={tab} />
    </div>
  );
}

function TabContent({ tab }) {
  let [fade, setFade] = useState("");
  useEffect(() => {
    let a = setTimeout(() => {
      setFade("end");
    }, 50);
    return () => {
      clearTimeout(a);
      setFade("");
    };
  }, [tab]);
  return (
    <div className={"start " + fade}>
      {[<div>내용0</div>, <div>내용1</div>, <div>내용2</div>][tab]}
    </div>
  );
}

export default Detail;
